import {IRecorderType} from '../Types';

export default async function getBlobDuration(blob: string) {
  const tempAudioElement = document.createElement('audio');

  tempAudioElement.src =
    typeof blob === 'string' ? blob : window.URL.createObjectURL(blob);

  const duration = new Promise((resolve, reject) => {
    tempAudioElement.addEventListener('loadedmetadata', () => {
      if (tempAudioElement.duration === Infinity) {
        tempAudioElement.currentTime = Number.MAX_SAFE_INTEGER;
        tempAudioElement.ontimeupdate = () => {
          tempAudioElement.ontimeupdate = null;
          resolve(tempAudioElement.duration);
          tempAudioElement.currentTime = 0;
        };
      } else resolve(tempAudioElement.duration);
    });

    tempAudioElement.onerror = (event: Event) =>
      reject((event.target as HTMLAudioElement).error);
  });

  return duration;
}

export function createRecorderFile(blob: Blob, record: IRecorderType) {
  const extension = getRecordExtension(blob.type);

  const fileName = `${record}-${new Date().getTime()}.${extension}`;
  const recordFile = new File([blob], fileName, {
    type: blob.type,
  });

  return recordFile;
}

export function getRecordExtension(mimeType: string) {
  const EXTRACT_TYPE_REGEXP = /^\s*([^;\s]*)(?:;|\s|$)/;
  const match = EXTRACT_TYPE_REGEXP.exec(mimeType);

  const Mimetypes = {
    'video/ogg': 'ogg',
    'video/mp4': 'mp4',
    'video/x-matroska': 'mkv',
    'video/webm': 'webm',
    'audio/mp4': 'm4a',
    'audio/mpeg': 'mp3',
    'audio/aac': 'aac',
    'audio/flac': 'flac',
    'audio/ogg': 'ogg',
    'audio/wav': 'wav',
    'audio/webm': 'webm',
  };

  const result = match && match[1].toLowerCase();
  return Mimetypes[result];
}
