import React from 'react';
import CN from 'classnames';
import useOutsideClick from '../../Hooks/useOutsideClick';
import {IAction, LanguageType} from '../../Types';

import './style.scss';

interface Props {
  id: string;
  actions: IAction[];
  language: LanguageType;
  _e: Localization._e;
  showOnHover?: boolean;
  Icon: React.FunctionComponent<React.SVGProps<SVGSVGElement>>;
}

const Options: React.FC<Props> = ({
  id,
  actions,
  _e,
  language,
  Icon,
  showOnHover = false,
}) => {
  const overflowMenuRef = React.useRef<HTMLDivElement>(null);
  const optionsRef = React.useRef<HTMLDivElement>(null);
  const elId = `actions-${id}`;

  // set option
  const [option, toggleOption] = React.useState(false);

  function clickHandler(event: React.MouseEvent<HTMLDivElement>) {
    event.preventDefault();

    toggleOption(!option);
  }

  useOutsideClick(optionsRef, () => toggleOption(false));

  React.useEffect(() => {
    if (option) {
      const listEl = overflowMenuRef.current;
      if (listEl) {
        const listElRec = listEl.getBoundingClientRect();
        // @ts-ignore
        const elRec = listEl.parentNode?.getBoundingClientRect();

        const yPos = elRec.top + Math.min(20, elRec.height);
        let xPos = elRec.left + elRec.width - listElRec.width;

        if ('ar' === language) {
          xPos = elRec.left;
        }

        listEl.style.setProperty('top', `${yPos}px`);
        listEl.style.setProperty('left', `${xPos}px`);
        listEl.style.setProperty('visibility', 'visible');
      }
    }

    // eslint-disable-next-line
  }, [option]);

  React.useEffect(() => {
    const el = document.getElementById(elId);

    if (el) {
      const resizeObserver = new IntersectionObserver(() =>
        toggleOption(false),
      );

      resizeObserver.observe(el);

      return () => {
        resizeObserver.unobserve(el);
      };
    }

    // eslint-disable-next-line
  }, []);

  if (!actions.length) {
    return null;
  }

  return (
    <div id={elId} className="options" onClick={clickHandler} ref={optionsRef}>
      <button className={CN({'show-on-hover': showOnHover})}>
        <Icon />
      </button>

      {option && (
        <div id={`${elId}-menu`} ref={overflowMenuRef}>
          {actions.map((action, index) => (
            <button key={`action-${id}-${index}`} onClick={action.callback}>
              <action.Icon />
              <span>{_e(action.label)}</span>
            </button>
          ))}
        </div>
      )}
    </div>
  );
};

export default Options;
