function displayDuration(duration: number): string {
  const hours = Math.floor(duration / 3600);
  const minutes = Math.floor((duration - hours * 3600) / 60);
  const seconds = Math.floor(duration - hours * 3600 - minutes * 60);

  const arr = [minutes, seconds];

  if (hours > 0) {
    arr.unshift(hours);
  }

  return arr.map((nbr) => ('0' + nbr).slice(-2)).join(':');
}

export function generateLabel(currentTime: number, duration: number): string {
  const x = displayDuration(currentTime);

  return x;
}
