import React from 'react';

interface Props {
  progress: number;
  min: number;
  max: number;
  onChange: (
    e: React.ChangeEvent<HTMLProgressElement | HTMLInputElement>,
  ) => void;
}

export default function Porgress({progress, min, max, onChange}: Props) {
  return (
    <div className="ana-chat-player-progress">
      <input
        min={min}
        max={max}
        step={0.01}
        type="range"
        value={progress}
        onChange={(event) => onChange(event)}
      />
      <progress
        max={max}
        value={progress}
        className="ana-chat-progress-buffer"
      />
    </div>
  );
}
