import React from 'react';
import classNames from 'classnames';

export default function Camera({recorder, isActive}) {
  const videoRef = React.useRef(null);

  const cameraClasses = classNames({
    'ana-chat-recorder__video-camera-player': true,
    'ana-chat-recorder__active-camera': isActive,
  });

  React.useEffect(() => {
    videoRef.current.srcObject = recorder.stream;
    if (recorder.state === 'recording') {
      videoRef.current.muted = true;
      videoRef.current.volume = 0;
    }
  }, [recorder]);

  return (
    <div className={cameraClasses}>
      <video autoPlay ref={videoRef} />
    </div>
  );
}
