import React from 'react';
import CN from 'classnames';

import MessageItem from '../Message/Item';
import {getClassOfMessage} from '../../Helpers/Message';

import avatar from '../../Assets/avatar.png';

import {IMessage, IQuotedMessage} from '../../Types';

import './index.scss';

interface Props {
  _e: Localization._e;
  _d: Localization._d;
  message: IMessage | IQuotedMessage;
  isLoggedIn: boolean;
  textareaRef?: React.MutableRefObject<HTMLTextAreaElement>;
  setMessageToReply: (message: IMessage) => void;
}

const QuotedMessage: React.FC<Props> = ({
  _e,
  _d,
  message,
  isLoggedIn,
  setMessageToReply,
  textareaRef = null,
}) => {
  async function cancelHandler(event: React.MouseEvent<HTMLButtonElement>) {
    event.preventDefault();

    setMessageToReply(null);
  }

  const Time = () => {
    const dateEl = _d('date' in message ? message.date : message.created_at);

    const fullTime = dateEl.format('HH:mm • DD MMMM YYYY');

    let tiemString = dateEl.format('DD MMMM');

    if (dateEl.isToday()) {
      tiemString = dateEl.format('HH:mm');
    }

    return (
      <time
        dateTime={'date' in message ? message.date : message.created_at}
        title={fullTime}
      >
        {tiemString}
      </time>
    );
  };

  React.useEffect(() => {
    if (message && textareaRef?.current) {
      textareaRef.current.focus();
    }
  }, [message]);

  return (
    <div className={CN('quotted-message', {})}>
      <blockquote>
        {message.user ? (
          <>
            <img
              src={message.user.avatar}
              alt={`${message.user.first_name} ${message.user.last_name}`}
            />

            <div className="metas">
              <strong>{`${message.user.first_name} ${message.user.last_name}`}</strong>
              <Time />
            </div>
          </>
        ) : (
          <>
            <img src={avatar} alt={_e('deletedAccount')} />

            <div className="metas">
              <strong>{_e('deletedAccount')}</strong>
              <Time />
            </div>
          </>
        )}

        <div className={`content ${getClassOfMessage(message)}`}>
          <MessageItem message={message} _e={_e} isLoggedIn={isLoggedIn} />
        </div>
      </blockquote>

      <button type="button" onClick={cancelHandler}>
        &times;
      </button>
    </div>
  );
};

export default React.memo(QuotedMessage);
