import React from 'react';

import { getRelValueForUrl } from '../../Helpers/urls';

interface Props {
  text: string;
}

const Linkify: React.FC<Props> = ({text}) => {
  const parseString = (string: string) => {
    // Support all types of urls
    const pattern =
      /(http(s)?:\/\/.)?(www\.)?[\u0621-\u064A-a-zA-Z0-9@:%._~#=]{2,256}\.[a-z]{2,6}\b([\u0621-\u064A-a-zA-Z0-9@:%_.~#?&//=]*)/gim;
    // URLs starting with http://, https://, or ftp://
    const pattern1 =
      /(\b(https?|ftp):\/\/[\u0621-\u064A-A-Z0-9+&@#/%?=~_|!:,.;]*[\u0621-\u064A-A-Z0-9+&@#/%=~_|])/gim;
    // URLs starting with "www." (without // before it, or it'd re-link the ones done above).
    const pattern2 = /(^|[^/])(www\.[\S]+(\b|$))/gim;
    // Change email addresses to mailto:: links.
    const pattern3 = /(([a-zA-Z0-9\-_.])+@[a-zA-Z_]+?(\.[a-zA-Z]{2,6})+)/gim;
    if (string === '') {
      return string;
    }

    if (!string.match(pattern)) {
      return string;
    }

    const reactElements = [];
    const subStrings = splitText(pattern, string);

    for (const index in subStrings) {
      let element;
      const subString = subStrings[index];

      if (subString.match(pattern1)) {
        element = createLinkNode(subString, subString, index);
      } else if (subString.match(pattern2)) {
        element = createLinkNode(subString, `http://${subString}`, index);
      } else if (subString.match(pattern3)) {
        element = createMailNode(subString, index);
      } else {
        element = createTextNode(subString, index);
      }

      reactElements.push(element);
    }

    return reactElements;
  };

  const createTextNode = (text, key) => {
    return <span key={key}>{text}</span>;
  };

  const createLinkNode = (text, link, key) => {
    return (
      <a key={key} href={link} target="_blank" rel={getRelValueForUrl(link)}>
        {text}
      </a>
    );
  };

  const createMailNode = (mail, key) => {
    return (
      <a key={key} href={'mailto:' + mail}>
        {mail}
      </a>
    );
  };

  // Convert text to an array of strings, containing links and text
  const splitText = (pattern, string) => {
    const subStrings = [];
    let lastIndex = 0,
      match;

    while ((match = pattern.exec(string)) !== null) {
      if (match.index > lastIndex) {
        subStrings.push(string.substring(lastIndex, match.index));
      }

      lastIndex = match.index + match[0].length;

      subStrings.push(string.substring(lastIndex, match.index));
    }

    if (string.length > lastIndex) {
      subStrings.push(string.substring(lastIndex));
    }
    return subStrings;
  };

  return <span>{parseString(text)}</span>;
};

export default Linkify;
