import {v4} from 'uuid';
import emojiRegex from 'emoji-regex/RGI_Emoji';

import {IFileCategory, IMessage, IQuotedMessage, IUser} from '../Types';

export function isFileMessage(message: IMessage | IQuotedMessage): boolean {
  return !['text', 'emoji'].includes(message.type);
}

// export function isFileMessage(message: IMessage | IQuotedMessage): boolean {
//   return Boolean(message.file);
// }

export function generateFileMessage(
  file: File,
  currentUser: IUser,
  category: IFileCategory,
): IMessage {
  return {
    id: v4(),
    type: file.type,
    // uploading: true,
    user: currentUser,
    body: generateFileMessageBody(file.name, file.size),
    file: {
      name: file.name,
      size: file.size,
      url: '#',
      mime_type: file.type,
      accessibility: 'any',
      category: category,
      encodings: [],
    },
  };
}

export function generateFileMessageBody(
  name: string,
  size: number,
  url = '#',
  fileId = null,
): string {
  const sanitizedName = name.replace('|', '');

  if (fileId) {
    return [sanitizedName, size, url, fileId].join('|');
  }

  return [sanitizedName, size, url].join('|');
}

export function getMessageFileId(message: IMessage): IMessage['body'] {
  return getFileIdFromBody(message.body);
}

export function getFileIdFromBody(body: IMessage['body']): string {
  const [, , , fileId] = body.split('|');
  return fileId;
}

export function getClassOfMessage(
  message: IMessage | IQuotedMessage,
): 'image' | 'audio' | 'text' | 'emoji' | 'document' | 'video' {
  if (message.type === 'text') {
    return 'text';
  }

  if (message.type === 'emoji') {
    return 'emoji';
  }

  if (message?.file?.category === 'voice') {
    return 'audio';
  }

  if (
    message?.file?.category === 'video' ||
    message?.file?.category === 'screen'
  ) {
    return 'video';
  }

  switch (message?.file?.mime_type) {
    case 'image/png':
    case 'image/gif':
    case 'image/bmp':
    case 'image/jpeg':
      return 'image';

    case 'audio/mpeg':
    case 'audio/wav':
    case 'audio/ogg':
      return 'audio';

    default:
      return 'document';
  }
}

export function isOnlyEmojis(message: string): boolean {
  if (!message) return true;

  const noEmojis = message.replace(emojiRegex(), '');
  const noSpace = noEmojis.replace(/[\s\n]/gm, '');

  return !noSpace;
}
