import React from 'react';

import Message from '../Message';
import ScrollButton from './ScrollButton';
import {
  isFirstOfUser,
  isFirstInDay,
  getNumberMessagesAfterSpecificMessage,
} from '../../Helpers/MessageList';

import {
  INFINITE_SCROLL_TRIGGER_HEIGHT,
  SHOW_SCROLL_HEIGHT,
  SCROLL_INTERVAL_MS,
} from '../../Helpers/constants';
import throttle from '../../Helpers/throttle';
import useStore from '../../Hooks/useStore';

import {ReactComponent as ChatIcon} from '../../Assets/Icons/chat.svg';

import './style.scss';
import {IMessage} from '../../Types';

const List = (props, ref) => {
  const {
    _d,
    _e,
    role,
    currentUser,
    messages,
    language,
    deleteMessage,
    setMessageToEdit,
    setMessageToReply,
    isLoading,
    isLoggedIn,
    onLoadMore,
    resendMessage,
    isSupportedOffline,
  } = useStore();

  React.useImperativeHandle(ref, () => ({
    scrollToBottom: () => scrollToBottom(),
  }));

  const lastMessage = React.useRef<IMessage>(
    messages.length ? messages[messages.length - 1] : null,
  );

  const chatContainer = React.useRef<HTMLDivElement>(null);

  const [showScroll, setShowScroll] = React.useState(true);

  function backToBottom(event: React.MouseEvent<HTMLButtonElement>) {
    event.preventDefault();

    scrollToBottom();
  }

  function scrollToBottom(smooth = true) {
    const el = chatContainer.current as HTMLDivElement;

    if (!el) {
      return;
    }

    setTimeout(() => {
      if (!smooth) {
        el.scrollTop = el.scrollHeight;
        return;
      }

      el.scrollTo({
        left: 0,
        behavior: 'smooth',
        top: el.scrollHeight,
      });
    });
  }

  const scrollHandler = throttle(() => {
    const container = chatContainer.current;

    if (!container) {
      return;
    }

    if (
      container.scrollTop * -1 + container.clientHeight >
      container.scrollHeight - INFINITE_SCROLL_TRIGGER_HEIGHT
    ) {
      onLoadMore();
    }

    if (container.scrollTop * -1 < SHOW_SCROLL_HEIGHT) {
      setShowScroll(true);
    } else {
      setShowScroll(false);
    }
  }, SCROLL_INTERVAL_MS);

  React.useEffect(() => {
    if (showScroll) {
      lastMessage.current = messages.length
        ? messages[messages.length - 1]
        : null;
    }

    // eslint-disable-next-line
  }, [showScroll, messages]);

  if (isLoading && messages.length < 1) {
    return <div className="messages-list loading"></div>;
  }

  if (messages.length < 1) {
    return (
      <div className="messages-list empty">
        <ChatIcon />
        <span>{_e('noMsgsyet')}</span>
      </div>
    );
  }

  // !: Be careful
  // !: If you delete div.messages-list or div.messages-list-content or ul, you will disable the scroll.
  return (
    <div className="messages-list">
      <div
        ref={chatContainer}
        className="messages-list-content"
        onScroll={scrollHandler}
      >
        <ul>
          {messages
            .filter((message) => !message.isDeleted)
            .map((message, index) => (
              <Message
                _d={_d}
                _e={_e}
                role={role}
                userId={currentUser?.id}
                key={message.id}
                message={message}
                language={language}
                isLoggedIn={isLoggedIn}
                deleteMessage={deleteMessage}
                isFirstInDay={isFirstInDay(messages, index, _d)}
                setMessageToEdit={setMessageToEdit}
                isFirstOfUser={isFirstOfUser(messages, index)}
                setMessageToReply={setMessageToReply}
                resendMessage={resendMessage}
                isSupportedOffline={isSupportedOffline}
              />
            ))}
        </ul>
      </div>
      {!showScroll && (
        <ScrollButton
          onClick={backToBottom}
          count={getNumberMessagesAfterSpecificMessage(
            messages,
            lastMessage.current,
          )}
        />
      )}
    </div>
  );
};

export default React.forwardRef(List);
