import React from 'react';
import emojis from '@hsoub/emoji';

import {useStore} from '../../Hooks';
import {IEmoji} from '../../Types';

interface Props {
  appendEmoji: React.Dispatch<React.SetStateAction<string>>;
  onClose: () => void;
  toggler: HTMLElement;
  textareaRef: React.RefObject<HTMLTextAreaElement>;
  textareaHeight: number;
}

const Emojis: React.FC<Props> = ({
  appendEmoji,
  onClose,
  toggler,
  textareaRef,
  textareaHeight,
}) => {
  const {language} = useStore();

  const containerRef = React.useRef<HTMLDivElement>(null);
  const emojisContainerRef = React.useRef<HTMLDivElement>(null);
  const excludedEmojis = ['smiling face'];
  const excludedEmojisCategories = ['flags'];

  const clickHandler = (emoji: string) => {
    return (event: React.MouseEvent<HTMLButtonElement>) => {
      event.preventDefault();

      const textarea = textareaRef.current;
      const cursorPosition = textarea.selectionStart;

      appendEmoji((content) => {
        const newContent =
          content.substring(0, cursorPosition) +
          emoji +
          content.substring(cursorPosition);
        return newContent;
      });

      const newCursorPosition = cursorPosition + emoji.length;

      setTimeout(() => {
        textarea.setSelectionRange(newCursorPosition, newCursorPosition);
        textarea.focus();
      }, 0);
    };
  };

  const closeWhenClickOutside = (event: Event) => {
    if (
      containerRef.current &&
      !containerRef.current.contains((event?.target as Node) || null) &&
      !toggler.contains((event?.target as Node) || null) &&
      !textareaRef.current.contains((event?.target as Node) || null)
    ) {
      onClose();
    }
  };

  const scrollToEmoji = (category: string) => {
    const categoryRef = emojisContainerRef.current?.querySelector(
      `#${category}`,
    );
    if (categoryRef) {
      categoryRef.scrollIntoView({
        behavior: 'smooth',
        block: 'nearest',
      });
    }
  };

  React.useEffect(() => {
    window.addEventListener('click', closeWhenClickOutside);

    return () => {
      window.removeEventListener('click', closeWhenClickOutside);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const style = {
    bottom: `${Math.min((textareaHeight || 30) + 40, 120)}px`,
  };
  return (
    <div ref={containerRef} className="chat-emojis open" style={style}>
      <div className="chat-emojis-categories">
        {Object.keys(emojis).map((category) => {
          if (excludedEmojisCategories.includes(category)) return;
          return (
            <button
              key={category}
              className={`chat-emojis-category-item bg-${category}-emoji`}
              onClick={() => scrollToEmoji(category)}
            />
          );
        })}
      </div>
      <div
        className="chat-emojis-body"
        lang={language}
        ref={emojisContainerRef}
      >
        {Object.entries(emojis).map(
          ([categories, items]: [string, IEmoji[]]) => {
            if (categories === 'flags') return;
            return (items as IEmoji[]).map((item: IEmoji, index: number) => {
              if (excludedEmojis.includes(item.title)) return;
              return (
                <button
                  id={index === 0 ? categories : ''}
                  key={`${item.emoji}-${index}`}
                  onClick={clickHandler(item.emoji)}
                  title={item.title}
                >
                  {item.emoji}
                </button>
              );
            });
          },
        )}
      </div>
    </div>
  );
};

export default Emojis;
