import React from 'react';
import CN from 'classnames';

import useStore from '../../Hooks/useStore';
import {ACCESSIBILITY_AUTHENTICATION} from '../../Constants';
import {formatSize} from '../../Helpers/File';

import {ReactComponent as AudioIcon} from '../../Assets/Icons/audio.svg';
import {ReactComponent as ImageIcon} from '../../Assets/Icons/image.svg';
import {ReactComponent as FileIcon} from '../../Assets/Icons/file.svg';

import {IMessageFile} from '../../Types';

interface Props {
  name: IMessageFile['name'];
  size: IMessageFile['size'];
  type: IMessageFile['mime_type'];
}

const UnauthenticatedFileMessage: React.FC<Props> = ({name, size, type}) => {
  const {_e, onError} = useStore();

  function renderIcon() {
    switch (type) {
      case 'image/png':
      case 'image/gif':
      case 'image/bmp':
      case 'image/jpeg':
        return <ImageIcon />;

      case 'audio/mpeg':
      case 'audio/wav':
      case 'audio/ogg':
        return <AudioIcon />;

      default:
        return <FileIcon />;
    }
  }

  function showNotAuthenticatedError() {
    onError({
      type: ACCESSIBILITY_AUTHENTICATION,
      message: _e(`errors.${ACCESSIBILITY_AUTHENTICATION}`),
    });
  }

  return (
    <>
      <button className={CN('icon')} onClick={showNotAuthenticatedError}>
        {renderIcon()}
      </button>

      <div className="metas" onClick={showNotAuthenticatedError}>
        <strong>{name}</strong>
        <small>{formatSize(size)}</small>
      </div>
    </>
  );
};

export default UnauthenticatedFileMessage;
