import defaultLocals from '../Locales';

export const mergeTranslation = (
  translations = {
    ar: {},
    en: {},
  },
) => {
  if (!translations) {
    return defaultLocals;
  }

  return {
    ar: {
      ...defaultLocals.ar,
      ...translations.ar,
    },
    en: {
      ...defaultLocals.en,
      ...translations.en,
    },
  };
};
