import React from 'react';
import debounce from 'lodash/debounce';
import {IFile} from '../../Types';
import useOnScreen from '../../Hooks/useOnScreen';
import defaultVideoPoster from '../../Assets/videoPoster.png';

interface Props {
  file: IFile[];
  uploading?: boolean;
  poster: string;
}

const VideoMessage: React.FC<Props> = ({file, uploading = false, poster}) => {
  const videoRef = React.useRef(null);
  const videoContainerRef = React.useRef(null);
  const lastCurrentTime = React.useRef(0);
  const [isLoaded, setIsLoaded] = React.useState(false);
  const isVisible = useOnScreen(videoContainerRef);

  const [width, setWidth] = React.useState(0);
  const [remountKey, setRemountKey] = React.useState(0);

  React.useEffect(() => {
    const handleResize = debounce(() => {
      if (videoRef.current) {
        const newWidth = videoRef.current.clientWidth;
        setWidth(newWidth);
      }
    }, 10);

    const resizeObserver = new ResizeObserver(handleResize);

    if (videoRef.current) {
      resizeObserver.observe(videoRef.current);
    }

    return () => {
      if (videoRef.current) {
        resizeObserver.unobserve(videoRef.current);
      }
    };
  }, []);

  React.useEffect(() => {
    videoRef.current.addEventListener('loadedmetadata', () => {
      setIsLoaded(true);
    });
  }, [file, isLoaded]);

  React.useEffect(() => {
    if (file.length <= 1) {
      setRemountKey((prevKey) => prevKey + 1);
      videoRef.current.preload = 'metadata';
    }
  }, [file, uploading]);

  React.useEffect(() => {
    if (isVisible) {
      videoRef.current.preload = 'metadata';
    }
  }, [isVisible]);

  React.useEffect(() => {
    if (lastCurrentTime.current !== 0) {
      videoRef.current.currentTime = lastCurrentTime.current;
      videoRef.current.play();
    }

    videoRef.current.addEventListener('error', (e) => {
      if (e.target.error.code === e.target.error.MEDIA_ERR_NETWORK) {
        lastCurrentTime.current = videoRef.current.currentTime;
        setRemountKey((prevKey) => prevKey + 1);
      }
    });
  }, [remountKey]);

  const style = {
    height: width ? `${width / 1.77}px` : 'auto', // Aspect ration 16/9
  };

  console.log('here');

  return (
    <div className="message-item__video_container" ref={videoContainerRef}>
      <video
        controls
        key={remountKey}
        preload="none"
        ref={videoRef}
        poster={poster || defaultVideoPoster}
        style={style}
      >
        {file.map((source, index) => (
          <source
            key={index}
            src={`${source.url}?version=${new Date().getTime()}`}
            type={source.mime_type}
          />
        ))}
      </video>
      {(uploading || !isLoaded) && (
        <div className="video-loading" style={style} />
      )}
    </div>
  );
};

export default VideoMessage;
