import React from 'react';
import getBlobDuration from '../Helpers/blob';

export function useGetDuration(src: string) {
  const [duration, setDuration] = React.useState(0);

  React.useEffect(() => {
    let isMounted = true;

    async function getDuration() {
      try {
        const duration = (await getBlobDuration(src)) as number;
        if (isMounted) setDuration(duration);
      } catch (error) {
        console.log("Can't resolve the blob duration.");
      }
    }

    getDuration();

    return () => {
      isMounted = false;
    };
  }, [src]);

  return duration;
}
