import {generateFileMessageBody, isFileMessage} from './Message';
import {
  addMessage,
  addMessages,
  deleteMessage,
  getMessage,
  isMessageExist,
  updateMessage,
} from './MessageList';

import {IMessage, IMessageFile} from '../Types';

const ChatController = {
  messages: {
    getMessage: getMessage,
    addMessage: addMessage,
    addMessages: addMessages,
    isMessageExist: isMessageExist,
    updateMessage: updateMessage,
    deleteMessage: deleteMessage,
  },
  message: {
    createMessage: (message: IMessage): IMessage => {
      return {
        ...message,
        isPending: false,
        uploading: true,
      };
    },
    createMessageSuccess: (message: IMessage): IMessage => {
      delete message.uploading;
      delete message.isPending;
      return message;
    },
    createMessageFailed: (message: IMessage): IMessage => {
      return {
        ...message,
        uploading: false,
        isPending: true,
      };
    },
    updateMessage: (message: IMessage): IMessage => {
      return {
        ...message,
        isPending: false,
        uploading: true,
      };
    },
    updateMessageSuccess: (message: IMessage): IMessage => {
      delete message.uploading;
      delete message.isPending;
      return message;
    },
    updateMessageFailed: (message: IMessage): IMessage => {
      return {
        ...message,
        uploading: false,
        isPending: true,
      };
    },
    deleteFile: (message: IMessage): IMessage => {
      return {
        ...message,
        isDeleted: true,
      };
    },
    deleteFileFailed: (message: IMessage): IMessage => {
      delete message.isDeleted;
      return message;
    },
    uploadSuccess: (file: IMessageFile, message: IMessage): IMessage => {
      const {name, size, url, id, mime_type} = file;

      return {
        ...message,
        body: generateFileMessageBody(name, size, url, id),
        type: mime_type,
        file: file,
      };
    },

    uploadFailed: (message: IMessage): IMessage => {
      return {
        ...message,
        uploading: false,
        hasError: true,
      };
    },
    isFile: isFileMessage,
  },
};

export default ChatController;
