import React from 'react';
import RecordRTC, {MediaStreamRecorder} from 'recordrtc';
import useStore from './useStore';
import {IError, IRecorder, IRecorderType} from '../Types';
import {
  CAMERA_NOT_ALLOWED,
  CAMERA_NOT_FOUND,
  MIC_NOT_ALLOWED,
  MIC_NOT_FOUND,
  RECORDER_NOT_SUPPORT,
  SCREEN_NOT_ALLOWED,
  RECORD_ERROR,
} from '../Constants';
import {formatNumber} from '../Helpers/Common';

const initialState: IRecorder = {
  state: null,
  blob: null,
  url: null,
  stream: null,
  activeTarget: null,
};

export default function useRecorder(
  maxSize: number,
  maxTime: number,
  onError: (error: IError) => void,
) {
  const {_e} = useStore();
  const [recorder, setRecorder] = React.useState(initialState);
  const recorderRef = React.useRef(null);
  const bloobsRef = React.useRef([]);
  const isMaxRecorder = React.useRef(false);
  const isMaxRecorderTime = React.useRef(false);
  const isEdge = navigator.userAgent.indexOf('Edge') !== -1;
  const isFirefox = navigator.userAgent.indexOf('Firefox') !== -1;
  const isChrome = navigator.userAgent.indexOf('Chrome') !== -1;
  const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);

  const dateStarted = React.useRef(null);
  const voiceParams = {audio: true, video: false};
  const screenParams = {audio: true, video: {mediaSource: 'screen'}};
  const videoParams = {audio: true, video: true};

  const screenStream = React.useRef(null);
  const audioVideoStream = React.useRef(null);

  const recordingOptions = {
    recorderType: MediaStreamRecorder,
    numberOfAudioChannels: isEdge ? 1 : 2,
    disableLogs: true,
    timeSlice: 10,
    ondataavailable: function (blob: Blob) {
      handleAvailableBlobs(blob);
    },
  };

  const videoSetting = {
    type: 'video',
    ...(isChrome ? {mimeType: 'video/webm;codecs=VP8'} : {}),
    ...recordingOptions,
  };

  const voiceSetting = {
    type: 'audio',
    echoCancellation: true,
    ...recordingOptions,
  };

  const screenSetting = {
    type: 'video',
    ...(isChrome && {mimeType: 'video/webm;codecs=VP8'}),
    ...recordingOptions,
  };

  const options = {
    screen: screenSetting,
    voice: voiceSetting,
    video: videoSetting,
  };

  let totalElapsedTime = 0;

  async function start(target: IRecorderType) {
    bloobsRef.current = [];
    let stream: MediaStream | undefined;

    const userMediaParams =
      target === 'voice' || target === 'screen' ? voiceParams : videoParams;

    if (target === 'voice' || target === 'video') {
      try {
        audioVideoStream.current = await navigator.mediaDevices.getUserMedia(
          userMediaParams,
        );
      } catch (error) {
        handleRecordError(error, target);
        return;
      }
    }

    if (target === 'screen') {
      try {
        screenStream.current = await navigator.mediaDevices.getDisplayMedia(
          //@ts-ignore
          screenParams,
        );

        const isMicPresent = await requestMicrophone();

        const videoTrack = screenStream.current.getVideoTracks()[0];
        videoTrack.onended = () => {
          stop();
        };

        const screenTrackAudio = isMicPresent
          ? [
              ...screenStream.current.getTracks(),
              ...audioVideoStream.current.getTracks(),
            ]
          : [...screenStream.current.getTracks()];
        stream = new MediaStream(screenTrackAudio);
      } catch (error) {
        handleRecordError(error, target);
        return;
      }
    } else if (target === 'video') {
      stream = audioVideoStream.current;
      setRecorder((prevState) => ({
        ...prevState,
        stream: audioVideoStream.current,
      }));
    } else {
      stream = audioVideoStream.current;
    }

    //@ts-ignore
    recorderRef.current = RecordRTC(stream, options[target]);

    recorderRef.current.startRecording();
    dateStarted.current = new Date();

    setRecorder((prevState) => ({
      ...prevState,
      state: recorderRef.current.state,
      activeTarget: target,
    }));
  }

  function handleAvailableBlobs(blob: Blob) {
    const currentTime = new Date().getTime();
    const elapsedSinceStart = (currentTime - dateStarted.current) / 1000;
    totalElapsedTime += elapsedSinceStart;

    const fixedTime = isFirefox ? 0 : 1;

    if (totalElapsedTime - fixedTime >= maxTime * 60) {
      isMaxRecorderTime.current = true;
      clearRecordingInstance();
      pause('maxRecordingTime');
      return;
    }

    if (getAvailabeBlobsSize() + blob.size > maxSize * 1024) {
      isMaxRecorder.current = true;
      clearRecordingInstance();
      pause('maxRecordingSize');
      return;
    }

    bloobsRef.current.push(blob);

    dateStarted.current = currentTime;
  }

  function remove() {
    bloobsRef.current = [];
    isMaxRecorder.current = false;
    isMaxRecorderTime.current = false;

    setRecorder((prevState) => {
      return {
        ...prevState,
        state: null,
        url: null,
        blob: null,
        stream: null,
        activeTarget: null,
      };
    });

    clearRecordingInstance();
  }

  function clearRecordingInstance() {
    // clear share option of the browser.
    if (screenStream.current) {
      const screenTracks = screenStream.current.getTracks();

      screenTracks.forEach((track: MediaStreamTrack) => {
        track.stop();
      });
    }

    // clear accessing to mic and camera
    if (audioVideoStream.current) {
      const tracks = audioVideoStream.current.getTracks();

      tracks.forEach((track: MediaStreamTrack) => {
        track.stop();
      });
    }
  }

  function stop() {
    recorderRef.current.stopRecording((blobUrl: string) => {
      setRecorder((prevState) => {
        return {
          ...prevState,
          state: recorderRef.current.state,
          blob: recorderRef.current.getBlob(),
          url: blobUrl,
        };
      });
    });
    clearRecordingInstance();
  }

  function pause(params?: 'maxRecordingSize' | 'maxRecordingTime') {
    recorderRef.current.pauseRecording();

    if (recorder.stream !== null) {
      recorder.stream.getAudioTracks().forEach((track) => {
        track.enabled = false;
      });
    }

    setRecorder((prevState) => {
      const availableBlobs = getAvailableBlobs();

      return {
        ...prevState,
        state: recorderRef.current.state,
        blob: availableBlobs,
        url: URL.createObjectURL(availableBlobs),
      };
    });

    if (params) {
      onError({
        type: params,
        message: _e(`errors.${params}`),
        data:
          params === 'maxRecordingSize'
            ? formatNumber(maxSize / 1024)
            : maxTime,
      });
      return;
    }
  }

  function getRealTimeBlob() {
    if (!recorderRef.current) return null;

    recorderRef.current.pauseRecording();

    const audioBlob = getAvailableBlobs();

    remove();

    return {
      blob: audioBlob,
    };
  }

  function resume() {
    if (recorder.activeTarget === 'screen' && recorder.state === 'stopped') {
      return;
    }

    if (isMaxRecorder.current) {
      onError({
        type: 'maxRecordingSize',
        message: _e('errors.maxRecordingSize'),
        data: maxSize,
      });
      return;
    }

    if (isMaxRecorderTime.current) {
      onError({
        type: 'maxRecordingTime',
        message: _e('errors.maxRecordingTime'),
        data: maxTime,
      });
      return;
    }

    dateStarted.current = new Date().getTime();

    recorderRef.current.resumeRecording();

    if (recorder.stream !== null) {
      recorder.stream.getAudioTracks().forEach((track) => {
        track.enabled = true;
      });
    }

    setRecorder((prevState) => {
      return {
        ...prevState,
        state: recorderRef.current.state,
      };
    });
  }

  function getAvailableBlobs() {
    if (!bloobsRef.current.length) return null;

    const blobType = bloobsRef.current[0].type;

    const fullAudioBlob = new Blob(bloobsRef.current, {
      type: blobType,
    });

    return fullAudioBlob;
  }

  function getAvailabeBlobsSize() {
    if (!bloobsRef.current) return 0;

    return bloobsRef.current.reduce(
      (size, currentSize) => size + currentSize.size,
      0,
    );
  }

  function handleRecordError(error: DOMException, target: IRecorderType) {
    let errorType: string;

    switch (error.name) {
      case 'TypeError':
        errorType = RECORDER_NOT_SUPPORT;
        break;
      case 'NotFoundError':
      case 'NotReadableError':
        errorType = target === 'video' ? CAMERA_NOT_FOUND : MIC_NOT_FOUND;
        break;
      case 'NotAllowedError':
        errorType =
          target === 'video'
            ? CAMERA_NOT_ALLOWED
            : target === 'screen'
            ? SCREEN_NOT_ALLOWED
            : MIC_NOT_ALLOWED;
        break;
      default:
        errorType = RECORD_ERROR;
        break;
    }

    onError({type: errorType, message: _e(`errors.${errorType}`)});
  }

  async function requestMicrophone() {
    try {
      audioVideoStream.current = await navigator.mediaDevices.getUserMedia({
        audio: true,
      });
    } catch (error) {
      return false;
    }
    return true;
  }

  return {
    recorder,
    start,
    remove,
    pause,
    resume,
    stop,
    getRealTimeBlob,
  };
}
