import React from 'react';
import CN from 'classnames';

import { formatSize } from '../../Helpers/File';

import {IMessageFile} from '../../Types';

interface Props {
  uploading?: boolean;
  file: IMessageFile;
}

const ImageMessage: React.FC<Props> = ({file, uploading = false}) => {
  function download(event) {
    event.preventDefault();
    window.open(file.url);
  }

  function cancelUpload(event) {
    event.preventDefault();
  }

  return (
    <>
      <button
        className={CN('icon', {uploading})}
        onClick={uploading ? cancelUpload : download}
      >
        <img src={file.thumbnail_url || file.url} alt="" />
      </button>

      {file.url === '#' ? (
        <div className="metas">
          <strong>{file.name}</strong>
          <small>{formatSize(file.size)}</small>
        </div>
      ) : (
        <a href={file.url} target="_blank" rel="noreferrer" className="metas">
          <strong>{file.name}</strong>
          <small>{formatSize(file.size)}</small>
        </a>
      )}
    </>
  );
};

export default ImageMessage;
