import React from 'react';
import useOutsideClick from '../../Hooks/useOutsideClick';

interface Props {
  url: string;
  onClose: () => void;
}

export default function PreviewVideo({url, onClose}: Props) {
  const previewRef = React.useRef(null);

  useOutsideClick(previewRef, onClose);

  return (
    <div className="ana-chat-recorder__video-player" ref={previewRef}>
      <video controls>
        <source src={url} />
      </video>
    </div>
  );
}
