import { IMessage } from "../Types";

export function getMessage(messages: IMessage[], messageId: string): IMessage {
  return messages.find((message) => message.id === messageId);
}

export function addMessage(messages: IMessage[], newMessage: IMessage): IMessage[] {
  const newMessages = [...messages];

  if (!isMessageExist(messages, newMessage)) {
    newMessages.push(newMessage);
    return newMessages;
  }

  return updateMessage(messages, newMessage);
}

export function isMessageExist(messages: IMessage[], newMessage: IMessage): boolean {
  return !!messages.find((message) => message.id === newMessage.id);
}

export function updateMessage(messages: IMessage[], updatedMessage: IMessage): IMessage[] {
  return messages.map((message) => {
    if (message.id !== updatedMessage.id) {
      return message;
    }

    return {
      // ...message,
      ...updatedMessage,
    };
  });
}

export function deleteMessage(messages: IMessage[], messageId: string): IMessage[] {
  return messages.filter((message) => message.id !== messageId);
}

export function isFirstInDay(messages: IMessage[], index: number, _d: Localization._d): boolean {
  if (index < 1) {
    return true;
  }

  const previous = messages[index - 1];
  const current = messages[index];

  // TODO: use dayJS or compare date using vanilla js
  return !_d(previous.created_at).isSame(current.created_at, 'day');
}

export function isFirstOfUser(messages: IMessage[], index: number): boolean {
  if (index < 1) {
    return true;
  }

  const previous = messages[index - 1];
  const current = messages[index];

  return previous.user?.id !== current.user?.id;
}

export function addMessages(messages: IMessage[], storedMessages: IMessage[]): IMessage[] {
  const newMessages = [];
  let _storedMessage = [...storedMessages];

  for (const message of messages) {
    const storedMessageIndex = storedMessages.findIndex(
      (storedMessage) => storedMessage.id === message.id,
    );

    if (storedMessageIndex === -1) {
      newMessages.push(message);
      continue;
    }

    const storedMessage = storedMessages[storedMessageIndex];
    if (
      storedMessage.isPending ||
      storedMessage.uploading ||
      storedMessage.hasError
    ) {
      newMessages.push(storedMessage);
      _storedMessage = deleteMessage(_storedMessage, storedMessage.id);
      continue;
    }

    newMessages.push(message);
  }

  return [
    ...newMessages,
    ..._storedMessage.filter(
      ({ isPending, uploading, hasError }) => isPending || uploading || hasError,
    ),
  ];
}

export function getNumberMessagesAfterSpecificMessage(messages: IMessage[], message: IMessage): number {
  if (!message) {
    return 0;
  }

  const messageIndex = messages.findIndex((m) => m.id === message.id);

  if (messageIndex === -1) {
    return 0;
  }

  return messages.length - (messageIndex + 1);
}
