import React from 'react';
import CN from 'classnames';
import debounce from 'lodash/debounce';

import {generateLabel} from '../../Helpers/Audio';

import {ReactComponent as PlayIcon} from '../../Assets/Icons/playRecord.svg';
import {ReactComponent as PauseIcon} from '../../Assets/Icons/pausePlayer.svg';
import useOnScreen from '../../Hooks/useOnScreen';
import useIsOnLine from '../../Hooks/useIsOnLine';

import {IFile, IMessageFile} from '../../Types';

const NUMBER_OF_ATTEMPTS = 3;
const PROGRESS_MAX_VALUE = 1000;

interface Props {
  file: IMessageFile | IFile[];
  uploading?: boolean;
}

const AudioMessage: React.FC<Props> = ({file, uploading = false}) => {
  const isLoaded = React.useRef(false);
  const isMetaDataLoaded = React.useRef(false);
  const audioRef = React.useRef<HTMLAudioElement>(null);
  const buttonRef = React.useRef<HTMLButtonElement>(null);
  const audioDuration = React.useRef(0);
  const isVisible = useOnScreen(buttonRef);
  const isOnLine = useIsOnLine();
  const lastCurrentTime = React.useRef(0);
  const isMultiSources = Array.isArray(file) && file.length > 0;

  const [state, setState] = React.useState({
    progress: 0,
    loading: false,
    label: '00:00',
    retry: 0,
    isProgressUpdates: false,
  });

  function startAudio() {
    setState((state) => ({...state, loading: true}));
    if (!isLoaded.current) {
      audioRef.current.onerror = (event) => {
        isLoaded.current = false;
        errorHandler(event);
      };
      if (!isMultiSources) {
        audioRef.current.src = `${
          (file as IMessageFile).url
        }?version=${new Date().getTime()}`;
      }
      return;
    }
    playAudio();
  }

  function errorHandler(e) {
    switch (e.target.error.code) {
      case e.target.error.MEDIA_ERR_NETWORK:
        // A network error caused the audio download to fail
        if (state.retry <= NUMBER_OF_ATTEMPTS) {
          isLoaded.current = false;
          lastCurrentTime.current = parseInt(`${audioRef.current.currentTime}`);

          setState((state) => ({...state, retry: state.retry + 1}));
          startAudio();
        }
        break;
      case e.target.error.MEDIA_ERR_SRC_NOT_SUPPORTED:
        setState((state) => ({...state, loading: false}));
        break;
      default:
    }
  }

  const playAudio = () => {
    if (lastCurrentTime.current) {
      audioRef.current.currentTime = lastCurrentTime.current;
    }

    const playPromise = audioRef.current.play();
    if (playPromise !== undefined) {
      playPromise
        .then(function () {
          lastCurrentTime.current = 0;
          setState((state) => ({...state, loading: false, retry: 0}));
        })
        .catch(function (error) {
          setState((state) => ({...state, loading: true}));
        });
    }
  };

  function pauseAudio() {
    setState((state) => ({...state, loading: true}));
    audioRef.current.pause();
  }

  function updateCurrentTime(value) {
    const el = audioRef.current;

    const newCurrentTime =
      (parseInt(value) * audioDuration.current) / PROGRESS_MAX_VALUE;
    setState((state) => ({...state, isProgressUpdates: false}));

    el.currentTime = newCurrentTime;
  }

  const debouncedUpdateCurrentTime = React.useCallback(
    debounce(updateCurrentTime, 300),
    [],
  );

  React.useEffect(() => {
    return () => {
      debouncedUpdateCurrentTime.cancel();
    };
  }, []);

  function changeHandler(event) {
    const {value} = event.target;

    setState((state) => ({...state, progress: value, isProgressUpdates: true}));

    debouncedUpdateCurrentTime(value);
  }

  const calculateProgress = (currentTime, duration) => {
    return (PROGRESS_MAX_VALUE * currentTime) / duration;
  };

  const getProgress = (target) => {
    if (!target) {
      return 0;
    }

    const {currentTime} = target;

    if (
      isNaN(audioDuration.current) ||
      audioDuration.current === 0 ||
      isNaN(currentTime)
    ) {
      return 0;
    }

    return calculateProgress(currentTime, audioDuration.current);
  };

  const cancelUpload = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
    // TODO: CANCEL UPLOAD
  };

  React.useEffect(() => {
    const audioElement = audioRef.current;

    function pauseHandler() {
      setState((state) => ({...state, loading: false}));
    }

    function timeupdateHandler(event) {
      const progress = getProgress(event.target);
      const {currentTime} = event.target;

      if (isNaN(audioDuration.current) || isNaN(currentTime)) {
        return;
      }

      setState((state) => ({
        ...state,
        loading: false,
        label: generateLabel(currentTime, audioDuration.current),
        progress: state.isProgressUpdates ? state.progress : progress,
      }));
    }

    if (audioElement) {
      audioElement.addEventListener('ended', pauseHandler);
      audioElement.addEventListener('timeupdate', timeupdateHandler);
    }

    return () => {
      if (audioElement) {
        audioElement.removeEventListener('ended', pauseHandler);
        audioElement.removeEventListener('timeupdate', timeupdateHandler);
      }
    };
  }, []);

  React.useEffect(() => {
    const audioElement = audioRef.current;

    function canplaythroughHandler() {
      isLoaded.current = true;
    }

    if (audioElement) {
      audioElement.addEventListener('canplay', canplaythroughHandler);
    }

    return () => {
      if (audioElement) {
        audioElement.removeEventListener('canplay', canplaythroughHandler);
      }
    };
  }, []);

  React.useEffect(() => {
    function loadMetaData() {
      if (isMetaDataLoaded.current) return;

      isMetaDataLoaded.current = true;
      const {duration} = audioRef.current;
      audioDuration.current = isNaN(duration) ? 0 : duration;
      setState((state) => ({
        ...state,
        label: generateLabel(audioDuration.current, 0),
      }));
    }
    audioRef.current.addEventListener('loadedmetadata', loadMetaData);
  }, []);

  React.useEffect(() => {
    if (isVisible) {
      audioRef.current.preload = 'metadata';
    }
  }, [isVisible]);

  const style = {
    backgroundImage: `linear-gradient(90deg, #2ba9e1 0%, #2ba9e1 ${
      state.progress / 10
    }%, #ccc ${state.progress / 10}%)`,
  };

  const playing = !audioRef.current?.paused !== false;

  return (
    <>
      <audio ref={audioRef} preload="none">
        {isMultiSources &&
          file.map((source, index) => (
            <source key={source.url} src={source.url} type={source.mime_type} />
          ))}
      </audio>

      <button
        ref={buttonRef}
        className={CN('icon', {uploading: uploading || state.loading})}
        onClick={uploading ? cancelUpload : playing ? pauseAudio : startAudio}
      >
        {playing ? (
          <PauseIcon width={22} height={22} />
        ) : (
          !uploading && <PlayIcon width={20} height={22} />
        )}
      </button>

      <div className="metas">
        <small>{state.label}</small>
        <input
          min={0}
          step={1}
          max={PROGRESS_MAX_VALUE}
          type="range"
          value={state.progress}
          disabled={uploading}
          onChange={changeHandler}
          style={style}
        />
      </div>
    </>
  );
};

export default AudioMessage;
