import React from 'react';
import CN from 'classnames';

import Options from '../Options';
import MessageItem from './Item';

import {getClassOfMessage} from '../../Helpers/Message';

import {ReactComponent as ScheduleIcon} from '../../Assets/Icons/schedule.svg';
import {ReactComponent as ErrorIcon} from '../../Assets/Icons/error.svg';
import {ReactComponent as DotsIcon} from '../../Assets/Icons/dots.svg';
import {ReactComponent as CloseIcon} from '../../Assets/Icons/close.svg';

import {IAction, IMessage, LanguageType} from '../../Types';

interface Props {
  message: IMessage;
  actions: IAction[];
  language: LanguageType;
  isLoggedIn: boolean;
  _e: Localization._e;
  onDelete: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const MessageContent: React.FC<Props> = ({
  message,
  actions: _actions,
  language,
  isLoggedIn,
  _e,
  onDelete,
}) => {
  const actions = !message.uploading ? _actions : [];

  function renderOptions(message) {
    if (message.hasError) {
      return (
        <div
          onClick={onDelete}
          title={_e('SomethingWrong')}
          className="options"
        >
          <CloseIcon width="24px" height="24px" />
        </div>
      );
    }

    if (message.uploading) {
      return (
        <div className="options">
          <ScheduleIcon />
        </div>
      );
    }

    if (message.isPending) {
      return (
        <Options
          id={message.id}
          language={language}
          actions={actions}
          Icon={ErrorIcon}
          _e={_e}
        />
      );
    }

    return (
      <Options
        actions={actions}
        id={message.id}
        _e={_e}
        language={language}
        Icon={DotsIcon}
        showOnHover={true}
      />
    );
  }

  return (
    <div
      className={CN('content', getClassOfMessage(message), {
        error: message.hasError,
      })}
    >
      <MessageItem message={message} _e={_e} isLoggedIn={isLoggedIn} />

      {renderOptions(message)}
    </div>
  );
};

export default MessageContent;
