import React, {useState} from 'react';
import useStore from '../../Hooks/useStore';
import classNames from 'classnames';

import {ReactComponent as MicIcon} from '../../Assets/Icons/mic.svg';
import {ReactComponent as PauseIcon} from '../../Assets/Icons/pauseRecord.svg';
import {ReactComponent as DeleteIcon} from '../../Assets/Icons/delete.svg';
import {ReactComponent as ScreenPreviewIcon} from '../../Assets/Icons/previewScreen.svg';
import {ReactComponent as ResumeIcon} from '../../Assets/Icons/resumeRecording.svg';

import PreviewVideo from './previewVideo';
import Player from '../Player';
import Camera from './camera';
import {IRecorder, IRecorderType} from '../../Types';
import './style.scss';

interface Props {
  remove: () => void;
  resume: () => void;
  pause: () => void;
  recorder: IRecorder;
  target: IRecorderType;
}

const Recorder = ({remove, pause, resume, recorder, target}: Props) => {
  const {_e} = useStore();
  const [previewVideo, setPreviewVideo] = useState(false);

  const removeRecording = () => {
    remove();
    setPreviewVideo(false);
  };

  const recordClasses = classNames({
    'ana-chat-recorder': true,
    'video-recorder': recorder.activeTarget === 'video',
    'is-recording': recorder.state === 'recording',
    'is-paused': recorder.state === 'paused' || recorder.state === 'stopped',
  });

  const resumeRecordingClasses = classNames({
    'ana-chat-recorder__resume': true,
    'ana-chat-recorder__resume-desactivate':
      recorder.activeTarget === 'screen' && recorder.state === 'stopped',
  });

  React.useEffect(() => {
    return () => remove();
  }, []);

  return (
    <div className={recordClasses}>
      {recorder.activeTarget && (
        <div className="ana-chat-recorder__container">
          <div className="ana-chat-recorder__body">
            {recorder.state === 'recording' && (
              <div className="ana-chat-recorder__recording">
                <div className="ana-chat-recorder__indicator">
                  <div className="recording-pulse">
                    <span />
                  </div>
                  <span className="ana-chat-recorder__indicator_title">
                    {_e(`${target}Recording`)}
                  </span>
                </div>
                <label
                  className="ana-chat-recorder__pause"
                  title={_e('stopRecording')}
                >
                  <PauseIcon onClick={pause} width={22} height={22} />
                </label>
              </div>
            )}
            {(recorder.state === 'paused' || recorder.state === 'stopped') && (
              <div className="ana-chat-recorder__result">
                <label
                  className="remove-recording"
                  title={_e('removeRecording')}
                >
                  <DeleteIcon
                    onClick={removeRecording}
                    width={18}
                    height={18}
                  />
                </label>
                {target === 'voice' && (
                  <>
                    <Player src={recorder.url} />
                    <label
                      className="ana-chat-recorder__start-voice"
                      title={_e('resumeRecording')}
                    >
                      <MicIcon width={20} height={20} onClick={resume} />
                    </label>
                  </>
                )}
                {(target === 'video' || target === 'screen') && (
                  <>
                    <label
                      className="ana-chat-recorder__preview"
                      onClick={() => setPreviewVideo((prevState) => !prevState)}
                    >
                      <ScreenPreviewIcon width={18} height={18} />
                      <span>{_e('recorderPreview')}</span>
                    </label>
                    <label
                      className={resumeRecordingClasses}
                      title={_e('resumeRecording')}
                    >
                      {recorder.state === 'paused' && (
                        <ResumeIcon onClick={resume} />
                      )}
                    </label>
                  </>
                )}
              </div>
            )}
          </div>
        </div>
      )}
      <>
        {previewVideo && (
          <PreviewVideo
            url={recorder.url}
            onClose={() => setPreviewVideo(false)}
          />
        )}
      </>
      <Camera
        recorder={recorder}
        isActive={
          recorder.activeTarget === 'video' && recorder.state === 'recording'
        }
      />
    </div>
  );
};

export default Recorder;
