import React from 'react';
import throttle from 'lodash/throttle';

import useStore from './useStore';

import {TYPING_INTERVAL_MS} from '../Helpers/constants';


type useTypingReturnType = (value: string) => void


function useTyping(): useTypingReturnType {
  const {onTyping} = useStore();

  const throttledOnTyping = React.useCallback(
    throttle(onTyping, TYPING_INTERVAL_MS),
    [],
  );

  /** @type {React.MutableRefObject<string>} */
  const prevValueRef = React.useRef();

  /** @param {string} value */
  function typingHandler(value) {
    if (!value) {
      return;
    }

    if (prevValueRef.current !== value) {
      prevValueRef.current = value;
      throttledOnTyping();
    }
  }

  return typingHandler;
}

export default useTyping;
