import React from 'react';
import CN from 'classnames';
import {v4} from 'uuid';

import Emojis from './emojis';
import QuotedMessage from '../QuotedMessage';
import MultimediaInput from '../MultimediaInput';

import {useTyping} from '../../Hooks';

import userCan from '../../Helpers/userCan';
import {isCtrlEnter} from '../../Helpers/Keyboard';
import {isOnlyEmojis} from '../../Helpers/Message';
import useStore from '../../Hooks/useStore';
import useOnChangeEffect from '../../Hooks/useOnChangeEffect';

import {ReactComponent as SendIcon} from '../../Assets/Icons/send.svg';
import {ReactComponent as EmojiIcon} from '../../Assets/Icons/emoji.svg';
import {ReactComponent as CloseIcon} from '../../Assets/Icons/close.svg';

import './style.scss';

interface Props {
  onCreateMessage: () => void;
}

const MessageForm: React.FC<Props> = ({onCreateMessage}) => {
  const {
    _d,
    _e,
    role,
    isLoggedIn,
    createRecorderMessage,
    createMessage,
    updateMessage,
    messageToEdit,
    messageToReply,
    setMessageToEdit,
    setMessageToReply,
    currentUser,
    onInputValueChange,
    uploadHandler,
    validation,
  } = useStore();

  const textareaRef = React.useRef<HTMLTextAreaElement>(null);

  const emojiTogglerButton = React.useRef<HTMLButtonElement>(null);

  const [message, setMessage] = React.useState('');

  const [textareaHeight, setTextareaHeight] = React.useState(30);

  const [displayEmojis, setDisplayEmojis] = React.useState(false);

  const [recorderMessage, setRecorderMessage] = React.useState<Blob>(null);

  const voiceRef = React.useRef(null);

  const videoRef = React.useRef(null);

  const blobRef = React.useRef(null);

  const [recordState, setRecordState] = React.useState({
    state: null,
    target: null,
  });

  const onTyping = useTyping();

  function changeHandler(event: React.ChangeEvent<HTMLTextAreaElement>) {
    event.preventDefault();
    let content = event.target.value;

    onTyping(content);

    if (validation.messageMaxLength !== -1) {
      content = content.substring(0, validation.messageMaxLength);
    }

    setMessage(content);
  }

  async function uploadFileHandler(files: FileList) {
    if (files) {
      uploadHandler(files);
      onCreateMessage();
    }
  }

  function ctrlEnterHandler(event: React.KeyboardEvent<HTMLTextAreaElement>) {
    if (isCtrlEnter(event)) {
      submitHandler(event);
    }
  }

  function emojiHandler(event: React.MouseEvent<HTMLButtonElement>) {
    event.preventDefault();

    setDisplayEmojis(!displayEmojis);
  }

  function generateRecorderMessage(blobFile: Blob) {
    createRecorderMessage(blobFile, recordState.target);

    setRecordState(null);
    setRecorderMessage(null);

    if (voiceRef.current) {
      voiceRef.current.removeRecord();
    }

    if (videoRef.current) {
      videoRef.current.removeRecord();
    }

    onCreateMessage();

    return;
  }

  function submitHandler(
    ev: React.FormEvent<HTMLFormElement | HTMLTextAreaElement>,
  ) {
    ev.preventDefault();

    setDisplayEmojis(false);

    if (recorderMessage && recorderMessage.size) {
      generateRecorderMessage(recorderMessage);
      return;
    }

    if (recordState.state === 'recording') {
      const blob = blobRef.current.getAvailableBlobs();
      generateRecorderMessage(blob);
      return;
    }

    textareaRef.current?.focus();

    if (messageToEdit) {
      updateMessage({
        ...messageToEdit,
        body: message,
        type: isOnlyEmojis(message) ? 'emoji' : 'text',
        // uploading: true,
      });
    } else if (message.trim().length) {
      createMessage({
        id: v4(),
        body: message.replace(/^\s+|\s+$/g, ''),
        reply_to_message_id: (messageToReply && messageToReply.id) || null,
        reply_to_message_data: messageToReply
          ? {
              body: messageToReply.body,
              type: messageToReply.type,
              date: messageToReply.updated_at,
              user: messageToReply.user,
            }
          : null,
        type: isOnlyEmojis(message) ? 'emoji' : 'text',
        user: currentUser,
        // uploading: true,
      });
      onCreateMessage();
    }

    setMessage('');

    return;
  }

  function cancelHandler(ev) {
    ev.preventDefault();

    if (messageToEdit) {
      setMessage('');
      setMessageToEdit(false);
    }
  }

  React.useEffect(() => {
    if (messageToEdit) {
      setMessage(messageToEdit.body);
      textareaRef.current.focus();
    }

    // eslint-disable-next-line
  }, [messageToEdit]);

  React.useEffect(() => {
    const textAreaEl = textareaRef.current as HTMLTextAreaElement;

    if (textAreaEl) {
      textAreaEl.style.height = 'auto';
      textAreaEl.style.height = `${textAreaEl.scrollHeight}px`;
      setTextareaHeight(textAreaEl.scrollHeight);
    }

    // eslint-disable-next-line
  }, [message.length]);

  useOnChangeEffect(() => {
    onInputValueChange?.(message || recordState.state);
  }, [message]);

  if (!userCan(role, 'create_msg')) {
    return null;
  }

  function handleRecordingState(state: string, target: string) {
    setRecordState((prev) => ({
      ...prev,
      state: state,
      target: target,
    }));
  }

  return (
    <>
      {displayEmojis && (
        <Emojis
          appendEmoji={setMessage}
          onClose={() => setDisplayEmojis(false)}
          toggler={emojiTogglerButton.current}
          textareaRef={textareaRef}
          textareaHeight={textareaHeight}
        />
      )}

      {messageToReply && (
        <QuotedMessage
          _e={_e}
          _d={_d}
          textareaRef={textareaRef}
          message={messageToReply}
          setMessageToReply={setMessageToReply}
          isLoggedIn={isLoggedIn}
        />
      )}

      <form onSubmit={submitHandler} className="chat-form">
        <div className={CN('chat-form__inputs')}>
          {(recordState.state === null || recordState.state === 'inactive') && (
            <>
              <button
                ref={emojiTogglerButton}
                type="button"
                onClick={emojiHandler}
                className={CN('emojis', {
                  open: displayEmojis,
                })}
              >
                <EmojiIcon />
              </button>

              <textarea
                rows={1}
                name="content"
                value={message}
                ref={textareaRef}
                autoComplete="off"
                onChange={changeHandler}
                onKeyDown={ctrlEnterHandler}
                placeholder={_e('formPlaceholder')}
              />
              {messageToEdit && (
                <button
                  type="button"
                  title="Cancel"
                  className="cancel"
                  onClick={cancelHandler}
                >
                  <CloseIcon />
                </button>
              )}
            </>
          )}
          <MultimediaInput
            onRecordingComplete={(blob) => setRecorderMessage(blob)}
            onUploadFile={(files) => uploadFileHandler(files)}
            recordingState={handleRecordingState}
            onClearRecording={voiceRef}
            onBlobAvailable={blobRef}
          />
        </div>
        <>
          <hr />

          <button type="submit" title={_e('send')}>
            <SendIcon width={28} height={28} />
          </button>
        </>
      </form>
    </>
  );
};

export default MessageForm;
