import { internalDomains } from '../Assets/config.json';

export function isUrlFromInternalDomain(url: string): boolean {
  try {
    const urlObj = new URL(url);
    const urlHostname = urlObj.hostname;

    for (const domain of internalDomains) {
      if (urlHostname.endsWith(domain)) {
        return true;
      }
    }
  } catch (e) {
    return false;
  }

  return false;
}

export function getRelValueForUrl(url: string): string {
  let relValue = 'noopener noreferrer';

  if (!isUrlFromInternalDomain(url)) {
    relValue += ' nofollow';
  }

  return relValue;
}
