import React from 'react';
import classNames from 'classnames';
import useRecorder from '../../Hooks/useRecorder';
import {useStore} from '../../Hooks';
import useOutsideClick from '../../Hooks/useOutsideClick';

import {ReactComponent as MicIcon} from '../../Assets/Icons/mic.svg';
import {ReactComponent as ScreenRecorderIcon} from '../../Assets/Icons/screenRecorder.svg';
import {ReactComponent as VideoRecorderIcon} from '../../Assets/Icons/videoRecorder.svg';
import {ReactComponent as AttachIcon} from '../../Assets/Icons/attach.svg';

import Recorder from '../Recorder';
import {IRecorderType} from '../../Types';

interface Props {
  onRecordingComplete: (blob: Blob) => void;
  recordingState: (state: string, target: string) => void;
  onUploadFile: (file: FileList) => void;
  onClearRecording: React.RefObject<{
    removeRecord: (newState: string) => void;
  }>;
  onBlobAvailable?: React.RefObject<{
    getAvailableBlobs: () => Blob;
  }>;
}

const MultimediaInput = ({
  onRecordingComplete,
  recordingState,
  onClearRecording,
  onBlobAvailable,
  onUploadFile,
}: Props) => {
  const {
    _e,
    useVoiceRecorder,
    useScreenRecorder,
    useVideoRecorder,
    maxRecorderSize,
    maxRecorderTime,
    onError,
  } = useStore();

  const [show, setShow] = React.useState(false);
  const formList = React.useRef(null);

  const {recorder, start, resume, remove, pause, getRealTimeBlob} = useRecorder(
    maxRecorderSize,
    maxRecorderTime,
    onError,
  );

  function startRecording(target: IRecorderType) {
    start(target);
    setShow(false);
  }

  function resumeRecording() {
    resume();
  }

  function pauseRecording() {
    pause();
  }

  function removeRecord() {
    recordingState(null, null);
    remove();
  }

  function getAvailableBlobs() {
    const {blob} = getRealTimeBlob();
    return blob;
  }

  React.useImperativeHandle(onClearRecording, () => ({
    removeRecord,
  }));

  React.useImperativeHandle(onBlobAvailable, () => ({
    getAvailableBlobs,
  }));

  async function uploadFileHandler(ev: React.ChangeEvent<HTMLInputElement>) {
    ev.preventDefault();

    const {target} = ev;

    const files = target.files;

    if (files) {
      onUploadFile(files);
    }

    setShow(false);

    target.value = '';
  }

  React.useEffect(() => {
    onRecordingComplete(recorder.blob);
    recordingState(recorder.state, recorder.activeTarget);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [recorder.state]);

  const classes = classNames({
    'chat-form__menu': true,
    'chat-form__menu-show': show,
  });

  const allowRecorder = [
    useVoiceRecorder,
    useScreenRecorder,
    useVideoRecorder,
  ].some((value) => value === true);

  useOutsideClick(formList, () => setShow(false));

  return (
    <>
      <div className="chat-form__multimedia" ref={formList}>
        <div className={classes}>
          <label className="attach">
            <AttachIcon width={20} height={20} />
            <span>{_e('attachFiles')}</span>
            <input type="file" onChange={uploadFileHandler} multiple />
          </label>
          {useVoiceRecorder && (
            <label
              className="ana-chat-recorder__start-voice"
              onClick={() => startRecording('voice')}
            >
              <MicIcon width={20} height={20} />
              <span>{_e('voiceMessage')}</span>
            </label>
          )}
          {useVideoRecorder && (
            <label
              className="ana-chat-recorder__start-video"
              onClick={() => startRecording('video')}
            >
              <VideoRecorderIcon width={20} />
              <span>{_e('videoMessage')}</span>
            </label>
          )}
          {useScreenRecorder && (
            <label
              className="ana-chat-recorder__start-screen"
              onClick={() => startRecording('screen')}
            >
              <ScreenRecorderIcon width={20} height={20} />
              <span>{_e('screenRecorder')}</span>
            </label>
          )}
        </div>
        {recorder.state === null ? (
          allowRecorder ? (
            <label className="attach" onClick={() => setShow(!show)}>
              <AttachIcon width={20} height={20} />
            </label>
          ) : (
            <label className="attach">
              <AttachIcon width={20} height={20} />
              <input type="file" onChange={uploadFileHandler} multiple />
            </label>
          )
        ) : null}
      </div>
      <Recorder
        remove={removeRecord}
        pause={pauseRecording}
        resume={resumeRecording}
        recorder={recorder}
        target={recorder.activeTarget}
      />
    </>
  );
};

export default MultimediaInput;
