import React from 'react';
import CN from 'classnames';

import QuotedMessage from '../QuotedMessage';
import MessageContent from './MessageContent';
import {isFileMessage} from '../../Helpers/Message';
import userCan from '../../Helpers/userCan';
import {useNavigatorOnLine} from '../../Hooks';
import {UPDATE_MESSAGE, DELETE_MESSAGE} from '../../Helpers/constants';

import avatar from '../../Assets/avatar.png';

import {ReactComponent as SendIcon} from '../../Assets/Icons/send.svg';
import {ReactComponent as ReplyIcon} from '../../Assets/Icons/reply.svg';
import {ReactComponent as EditIcon} from '../../Assets/Icons/edit.svg';
import {ReactComponent as DeleteIcon} from '../../Assets/Icons/delete.svg';

import {IAction, IFeature, IMessage, LanguageType, RoleType} from '../../Types';

import './index.scss';

interface Props {
  message: IMessage;
  language: LanguageType;
  role: RoleType;
  userId: String;
  _e: Localization._e;
  _d: Localization._d;
  isFirstInDay: boolean;
  isFirstOfUser: boolean;
  isLoggedIn: boolean;
  setMessageToEdit: (message: IMessage | false) => void;
  setMessageToReply: (message: IMessage | null) => void;
  deleteMessage: (message: IMessage) => void;
  resendMessage: (message: IMessage) => void;
  isSupportedOffline: (action: IFeature) => boolean;
}

const Message: React.FC<Props> = ({
  _d,
  _e,
  role,
  userId,
  message,
  language,
  isLoggedIn,
  isFirstInDay,
  isFirstOfUser,
  deleteMessage,
  setMessageToEdit,
  setMessageToReply,
  resendMessage,
  isSupportedOffline,
}) => {
  const isOnline = useNavigatorOnLine();

  const [actions, setActions] = React.useState(getActions());

  function getActions(): IAction[] {
    const actions = [];

    if (message.isPending) {
      actions.push({
        label: 'resend',
        action: 'create_msg',
        Icon: SendIcon,
        callback: async (event) => {
          event.preventDefault();
          resendMessage(message);
        },
      });
    }

    if (!message.isPending) {
      actions.push({
        label: 'reply',
        action: 'reply',
        Icon: ReplyIcon,
        callback: async (event) => {
          event.preventDefault();
          setMessageToReply(message);
        },
      });
    }

    const isUpdateEnabled =
      (isOnline && !message.isPending) || isSupportedOffline(UPDATE_MESSAGE);

    if (!isFileMessage(message) && isUpdateEnabled) {
      actions.push({
        label: 'edit',
        action: 'edit',
        Icon: EditIcon,
        callback: async (event) => {
          event.preventDefault();
          setMessageToEdit(message);
        },
      });
    }

    const isDeleteEnabled =
      isOnline || message.isPending || isSupportedOffline(DELETE_MESSAGE);

    if (isDeleteEnabled) {
      actions.push({
        label: 'delete',
        action: 'delete',
        Icon: DeleteIcon,
        callback: deleteHandler,
      });
    }

    return actions.filter(({action}) =>
      userCan(role, action, Boolean(userId === message.user?.id)),
    );
  }

  function deleteHandler(event) {
    event.preventDefault();
    const isPendingOrHasError = message.isPending || message.hasError;
    if (isPendingOrHasError || window.confirm(_e('areYouSure'))) {
      deleteMessage(message);
    }
  }

  function QuotedMessageContent() {
    if (!message.reply_to_message_data) {
      return null;
    }

    return (
      <QuotedMessage
        _e={_e}
        _d={_d}
        message={message.reply_to_message_data}
        setMessageToReply={setMessageToReply}
        isLoggedIn={isLoggedIn}
      />
    );
  }

  const DaySeparator = () => {
    if (!isFirstInDay) {
      return null;
    }

    const dateEl = _d(message.created_at);

    let timeString = dateEl.format('DD MMMM');

    if (dateEl.isToday()) {
      timeString = _e('today');
    }

    if (dateEl.isYesterday()) {
      timeString = _e('yesterday');
    }

    return (
      <span className="day-separator">
        <small>{timeString}</small>
      </span>
    );
  };

  const Time = () => {
    const dateEl = _d(message.created_at);
    const isEdited = message.created_at < message.updated_at;
    const fullTime = dateEl.format('HH:mm • DD MMMM YYYY');

    return (
      <div className="message-time">
        <time title={fullTime} dateTime={message.created_at}>
          {dateEl.format('HH:mm')}
        </time>

        {isEdited && (
          <span title={_d(message.updated_at).format('HH:mm • DD MMMM YYYY')}>
            {_e('edited')}
          </span>
        )}
      </div>
    );
  };

  React.useEffect(() => {
    setActions(getActions());

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [message, isOnline]);

  return (
    <li
      id={message.id}
      className={CN('message-item', {
        'is-a-reply': message.reply_to_message_id,
        'first-in-group': isFirstOfUser || isFirstInDay,
      })}
    >
      <DaySeparator />

      {(isFirstOfUser || isFirstInDay) && message.user && (
        <>
          <img
            src={message.user.avatar}
            alt={`${message.user.first_name} ${message.user.last_name}`}
          />

          <div className="metas">
            <strong>{`${message.user.first_name} ${message.user.last_name}`}</strong>
            <Time />
          </div>
        </>
      )}

      {(isFirstOfUser || isFirstInDay) && !message.user && (
        <>
          <img src={avatar} alt={_e('deletedAccount')} />

          <div className="metas">
            <strong>{_e('deletedAccount')}</strong>
            <Time />
          </div>
        </>
      )}

      {!(isFirstOfUser || isFirstInDay) && <Time />}

      <QuotedMessageContent />

      <MessageContent
        message={message}
        _e={_e}
        language={language}
        isLoggedIn={isLoggedIn}
        actions={actions}
        onDelete={deleteHandler}
      />
    </li>
  );
};

export default React.memo(Message, (prevProps, nextprops) => {
  return (
    prevProps.language === nextprops.language &&
    prevProps.message === nextprops.message &&
    prevProps.isFirstInDay === nextprops.isFirstInDay &&
    prevProps.isFirstOfUser === nextprops.isFirstOfUser
  );
});
