import React from 'react';

type Callback = () => void;

function useOnChangeEffect(callback: Callback, value: any): void {
  const ref = React.useRef(value);

  React.useEffect(() => {
    if (value !== ref.current) {
      ref.current = value;
      callback();
    }
  }, [callback, value]);
}

export default useOnChangeEffect;
