import React from 'react';

import AudioMessage from './audio';
import DocumentMessage from './document';
import EmojiMessage from './emoji';
import ImageMessage from './image';
import TextMessage from './text';
import VideoMessage from './video';

import {IMessage, IQuotedMessage} from '../../Types';
import UnauthenticatedFileMessage from './unauthenticatedFileMessage';

interface Props {
  message: IMessage | IQuotedMessage;
  _e: Localization._e;
  isLoggedIn: boolean;
}

const MessageItem: React.FC<Props> = ({message, _e, isLoggedIn}) => {
  if (message.type === 'text' || message.type === 'emoji') {
    if (!message.body) {
      return (
        <div className="content deleted">
          <span>{_e('deletedMessage')}</span>
        </div>
      );
    }

    if (message.type === 'emoji') {
      return <EmojiMessage body={message.body} />;
    }

    return <TextMessage body={message.body} />;
  }

  if (message?.file) {
    if (message.file.accessibility === 'authenticated' && !isLoggedIn) {
      return (
        <UnauthenticatedFileMessage
          name={message.file.name}
          size={message.file.size}
          type={message.file.mime_type}
        />
      );
    }

    if (message?.file.category === 'file') {
      switch (message.file.mime_type) {
        case 'image/png':
        case 'image/gif':
        case 'image/bmp':
        case 'image/jpeg':
          return (
            <ImageMessage
              file={message.file}
              uploading={'uploading' in message ? message.uploading : false}
            />
          );

        case 'audio/mpeg':
        case 'audio/wav':
        case 'audio/ogg':
          return (
            <AudioMessage
              file={message.file}
              uploading={'uploading' in message ? message.uploading : false}
            />
          );

        default:
          return (
            <DocumentMessage
              file={message.file}
              uploading={'uploading' in message ? message.uploading : false}
            />
          );
      }
    }

    const sourceFile = [...message?.file.encodings, message?.file];

    if (message?.file.category === 'voice') {
      return (
        <AudioMessage
          file={sourceFile}
          uploading={'uploading' in message ? message.uploading : false}
        />
      );
    }

    if (
      message?.file.category === 'screen' ||
      message?.file.category === 'video'
    ) {
      return (
        <VideoMessage
          file={sourceFile}
          uploading={'uploading' in message ? message.uploading : false}
          poster={message?.file.thumbnail_url}
        />
      );
    }
  }

  return (
    <div className="content deleted">
      <span>{_e('deletedFile')}</span>
    </div>
  );
};

export default MessageItem;
