import React from 'react';

import {ReactComponent as PlayIcon} from '../../Assets/Icons/playRecord.svg';
import {ReactComponent as PauseIcon} from '../../Assets/Icons/pausePlayer.svg';

import Timer from './timer';
import Porgress from './progress';
import {useGetDuration} from '../../Hooks/useGetDuration';
import './style.scss';
import {PROGRESS_MAX_VALUE} from '../../Constants';

interface Props {
  src: string;
  controls?: boolean;
}

export default function Player({src, controls = false}: Props) {
  const audioRef = React.useRef(null);
  const duration = useGetDuration(src);

  const [state, setState] = React.useState({
    progress: 0,
    volume: 1,
    currentTime: 0,
    duration: duration,
  });

  function changeHandler(event) {
    const {value} = event.target;
    setState((state) => ({...state, progress: value}));

    updateCurrentTime(value);
  }

  function updateCurrentTime(value) {
    const el = audioRef.current;
    const newCurrentTime =
      (parseFloat(value) * state.duration) / PROGRESS_MAX_VALUE;

    el.currentTime = newCurrentTime;
  }

  function startAudio() {
    if (audioRef.current) audioRef.current.play();
  }

  function pauseAudio() {
    if (audioRef.current) audioRef.current.pause();
  }

  function changeVolume(event) {
    const {value} = event.target;
    setState((state) => ({...state, volume: value}));
    audioRef.current.volume = value;
  }

  React.useEffect(() => {
    setState((state) => ({...state, duration: duration}));
  }, [duration]);

  React.useEffect(() => {
    const audioElement = audioRef.current;

    function timeupdateHandler(event) {
      const {currentTime} = event.target;
      if (!isNaN(currentTime) && currentTime !== 0) {
        setState((state) => ({
          ...state,
          progress: (PROGRESS_MAX_VALUE * currentTime) / state.duration,
          currentTime: currentTime,
        }));
      }
    }

    if (audioElement) {
      audioElement.addEventListener('timeupdate', timeupdateHandler);
    }

    return () => {
      if (audioElement) {
        audioElement.removeEventListener('timeupdate', timeupdateHandler);
      }
    };
  }, []);

  const playing = !audioRef.current?.paused !== false;

  return (
    <div className="ana-chat-player">
      <audio src={src} ref={audioRef} />
      <button onClick={playing ? pauseAudio : startAudio} type="button">
        {playing ? (
          <label className="ana-chat-player__pause">
            <PauseIcon width={22} height={22} />
          </label>
        ) : (
          <label>
            <PlayIcon
              width={20}
              height={22}
              className="ana-chat-player__play"
            />
          </label>
        )}
      </button>
      <Porgress
        progress={state.progress}
        onChange={changeHandler}
        min={0}
        max={PROGRESS_MAX_VALUE}
      />
      <Timer currentTime={state.currentTime} duration={state.duration} />
      {controls && (
        <div className="player-volume">
          <Porgress
            progress={state.volume}
            onChange={changeVolume}
            min={0}
            max={1}
          />
        </div>
      )}
    </div>
  );
}
