import {permissions} from '../Assets/config.json';
import { RoleType } from '../Types';

export default function userCan(role: RoleType, action: string, isOwner = false): boolean {
  if (!(role in permissions)) {
    return false;
  }

  const actions = [action, `${action}_others`];

  if (isOwner) {
    actions.push(`${action}_own`);
  }

  for (const action of actions) {
    if (!permissions[role].includes(action)) {
      continue;
    }

    return true;
  }

  return false;
}
