import Polyglot from 'node-polyglot';
import _d from './_d';

import { ConfigType } from 'dayjs';
import { LocalizationType, LangPhrases, options } from './types';

export default function anaLocalization(locale: string, langPhrases: LangPhrases): LocalizationType {
  const polyglot = new Polyglot({
    phrases: langPhrases,
    locale: locale,
  });

  const _e = (string: string, rule?: options, fallBackString: string = null) => {
    if (polyglot.has(string)) {
      return polyglot.t(string, rule);
    }

    console.warn('Missing translation', string);

    if (fallBackString) {
      return _e(fallBackString, rule);
    }

    return string;
  };

  return {
    _e,
    _d: (args?: ConfigType) => _d(args, locale),
    language: locale,
  };
}