import dayjs from "dayjs";

import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import isToday from 'dayjs/plugin/isToday';
import isTomorrow from 'dayjs/plugin/isTomorrow';
import isYesterday from 'dayjs/plugin/isYesterday';
import relativeTime from './relativeTime';
import updateLocale from 'dayjs/plugin/updateLocale';
import calendar from 'dayjs/plugin/calendar';

import './locale/ar';
import 'dayjs/locale/en';

dayjs.extend(updateLocale);
dayjs.extend(relativeTime);
dayjs.extend(utc);
dayjs.extend(isToday);
dayjs.extend(isTomorrow);
dayjs.extend(isYesterday);
dayjs.extend(timezone);
dayjs.extend(calendar);

dayjs.updateLocale('en', {
    calendar: {
        sameDay: 'h:mm A',
        nextDay: '[Tomorrow]',
        nextWeek: 'dddd',
        lastDay: '[Yesterday]',
        lastWeek: '[Last] dddd',
        sameElse: 'D MMMM',
    }
});


// const myTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
const myTimeZone = dayjs.tz.guess();
// const myTimeZone = 'Africa/Casablanca';
dayjs.tz.setDefault(myTimeZone);

/**
 * Returns a dayjs object with the specified configuration and locale.
 *
 * If `locale` is defined, returns a dayjs object with the specified `args` and locale. If `locale`
 * is undefined or empty, returns a dayjs object with the specified `args` and the default locale.
 * 
 * !: Note that `dayjs(args).locale(locale)` returns a string if `locale` is undefined or empty, so the `_d` function checks for this case and only applies the locale if it is defined.
 *
 * @param args The configuration for the dayjs object, as a string or Date object.
 * @param locale The locale to use for the dayjs object, as a string.
 * @returns A dayjs object with the specified configuration and locale.
 */
export default function _d(args?: dayjs.ConfigType, locale?: string) {
    if (locale) {
        return dayjs(args).locale(locale);
    }

    return dayjs(args);
}


export { dayjs }