import React from 'react';

import MessageForm from '../MessageForm';
import List from '../List';
import DropZone from '../DropZone';
import useStore from '../../Hooks/useStore';

const Container = () => {
  const {uploadHandler} = useStore();

  const listElement = React.useRef<null | HTMLDivElement>(null);

  function dropHandler(event: React.DragEvent<HTMLDivElement>) {
    uploadHandler(event.dataTransfer.files);
  }

  function createMessageHandler() {
    if (listElement.current) {
      // @ts-ignore
      listElement.current?.scrollToBottom();
    }
  }

  return (
    <DropZone onDrop={dropHandler}>
      <div className="ana-react-chat">
        <List ref={listElement} />
        <MessageForm onCreateMessage={createMessageHandler} />
      </div>
    </DropZone>
  );
};

export default Container;
