import React from 'react';
import anaLocalization from '@hsoub/ana-localization';

import * as MessageList from './MessageList';
import {generateFileMessage, getMessageFileId, isFileMessage} from './Message';
import {mergeTranslation} from './Translation';
import {createRecorderFile} from './blob';

import {
  AnaReactChat,
  AppContextType,
  IError,
  IFeature,
  IFileCategory,
  IMessage,
  INotification,
  IRecorderType,
} from '../Types';

// @ts-ignore
export const ChatStore = React.createContext<AppContextType>();

type Props = AnaReactChat & {
  children: React.ReactNode;
};

const StoreProvider: React.FC<Props> = ({
  role,
  language,
  messages,
  typists = [],
  children,
  isLoading = false,
  onlineOnly = [],
  useVoiceRecorder = false,
  useScreenRecorder = false,
  useVideoRecorder = false,
  maxRecorderSize = 102400,
  maxRecorderTime = 5,
  onInputValueChange,
  onCreateMessage = (message: IMessage) => {},
  onUpdateMessage = (message: IMessage, previousMessage?: IMessage) => {},
  onDeleteMessage = (message: IMessage, fileId?: string) => {},
  onUpload = (file: File, message: IMessage, category: IFileCategory) => {},
  onLoadMore = () => {},
  onTyping = () => {},
  onError = (error: IError) => {},
  currentUser,
  translations,
  validation = {
    messageMaxLength: -1,
  },
}) => {
  const {_e, _d} = anaLocalization(
    language,
    // @ts-ignore
    mergeTranslation(translations)[language],
  );

  const [storeData, setStoreData] = React.useState<{
    messageToEdit: IMessage | false;
    messageToReply: IMessage | null;
  }>({
    messageToEdit: false,
    messageToReply: null,
  });

  function createMessage(newMessage: IMessage) {
    onCreateMessage(newMessage);

    resetState();
  }

  function updateMessage(updatedMessage: IMessage) {
    const previousMessage = MessageList.getMessage(messages, updatedMessage.id);
    onUpdateMessage(updatedMessage, previousMessage);

    resetState();
  }

  function deleteMessage(deletedMessage: IMessage) {
    if (isFileMessage(deletedMessage)) {
      const fileId = getMessageFileId(deletedMessage);
      if (fileId) {
        onDeleteMessage(deletedMessage, fileId);
        return;
      }
    }

    onDeleteMessage(deletedMessage);

    resetState();
  }

  function resendMessage(message: IMessage) {
    if (message.created_at) {
      onUpdateMessage(message);
    } else {
      onCreateMessage(message);
    }
  }

  function setMessageToEdit(messageToEdit: IMessage | false) {
    setStoreData((state) => ({
      ...state,
      messageToEdit,
    }));
  }

  function setMessageToReply(messageToReply: IMessage | null) {
    setStoreData((state) => ({
      ...state,
      messageToReply,
    }));
  }

  function resetState() {
    setStoreData((storeData) => ({
      ...storeData,
      messageToEdit: false,
      messageToReply: null,
    }));
  }

  function isSupportedOffline(action: IFeature): boolean {
    return !onlineOnly.includes(action);
  }

  function uploadHandler(files: FileList) {
    if (!files || !files.length) {
      return;
    }

    Object.values(files).forEach((file) => {
      if (!file) {
        return;
      }

      const newMessage = generateFileMessage(file, currentUser, 'file');
      // createMessage(newMessage);

      onUpload(file, newMessage, 'file');
    });
  }

  function createRecorderMessage(blob: Blob, type: IRecorderType) {
    const file = createRecorderFile(blob, type);
    const newMessage = generateFileMessage(file, currentUser, type);

    onUpload(file, newMessage, type);
  }

  return (
    <ChatStore.Provider
      value={{
        _d,
        _e,
        role,
        messages,
        typists,
        language,
        useVoiceRecorder,
        useScreenRecorder,
        useVideoRecorder,
        maxRecorderSize,
        maxRecorderTime,
        ...storeData,
        createMessage,
        createRecorderMessage,
        deleteMessage,
        updateMessage,
        onTyping,
        onLoadMore,
        setMessageToEdit,
        setMessageToReply,
        resendMessage,
        isLoading,
        currentUser,
        isLoggedIn: Boolean(currentUser),
        uploadHandler,
        validation,
        isSupportedOffline,
        onError,
        onInputValueChange,
      }}
    >
      {children}
    </ChatStore.Provider>
  );
};

export default StoreProvider;
